.filter-btn{z-index:100;top:90px;left:0}.custom-select{color:var(--slicknav__font);background:#2c3e50}select{-webkit-appearance:none;-moz-appearance:none;-ms-appearance:none;appearance:none;outline:0;box-shadow:none;border:0 !important;background:#2c3e50;background-image:none}select::-ms-expand{display:none}.select{position:relative;display:flex;width:100%;height:3em;line-height:3;background:#2c3e50;overflow:hidden;border-radius:.25em}select{flex:1;padding:0 .5em;color:#fff;cursor:pointer}.select::after{content:"▼";position:absolute;top:0;right:0;padding:0 1em;background:#34495e;cursor:pointer;pointer-events:none;-webkit-transition:.25s all ease;-o-transition:.25s all ease;transition:.25s all ease}.select:hover::after{color:#f39c12}.bg-light{background-color:var(--card__background) !important}